<template>
  <Loading
    :class="{
      'content--hide': !showLoading,
      'content--show': showLoading,
    }"
    v-if="displayLoading"
  />
  <div
    :class="{
      'markdown-content': true,
      'content--hide': !showContent,
      'content--show': showContent,
    }"
    v-if="displayContent"
  >
    <div class="markdown-body" v-html="htmlContent"></div>
  </div>
</template>

<script>
import { ref, onMounted } from "vue";
import md from "markdown-it";
import Loading from "../components/Loading";
import data from "!raw-loader!../assets/md/about.md";

let showLoading = ref(true);
let showContent = ref(false);
let displayLoading = ref(true);
let displayContent = ref(false);
const htmlContent = ref("");

export default {
  name: "About",
  components: { Loading },
  setup() {
    onMounted(async () => {
      if (showLoading.value) {
        const mdConfig = md({
          html: true,
          linkify: true,
          typographer: true,
        });
        const result = mdConfig.render(`${data}`);
        htmlContent.value = result;

        setTimeout(() => {
          // Á≠âÂæÖ v-if ÊïàÊûú
          showLoading.value = false;
          setTimeout(() => {
            // Á≠âÂæÖloadingÂãïÁï´ÁµêÊùü
            displayLoading.value = false;
            displayContent.value = true;
            setTimeout(() => {
              // Á≠âÂæÖ v-if ÊïàÊûú
              showContent.value = true;
            }, 100);
          }, 500);
        }, 500);
      }
    });
    return {
      showLoading,
      showContent,
      displayLoading,
      displayContent,
      htmlContent,
    };
  },
};
</script>
<style lang="scss">
@import "../style/md.scss";
.content--hide {
  opacity: 0;
}

.content--show {
  opacity: 1;
}
</style>
